/* add css module styles here (optional) */

._3o8gR {
  display: none !important;
}

._2Vept {
  width: 40px;
  display: inline-block;
}

._2Vept svg {
  width: 100%;
  height: auto;
}

._2Vept img {
  width: 100%;
  height: auto;
}

._Xt-75 {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}

._1QpeU {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.45);
}

._3bARu {
  background: white;
  z-index: 1001;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 15px 15px;
}

.dark ._3bARu {
  background: #1b1d1c;
}

._ROKAa {
  display: grid;
  grid-template-columns: 150px 150px;
  max-height: 100%;
  overflow: auto;
  gap: 15px;
}

._3fqTI {
  padding: 5px 30px;
  border: 1px solid rgba(195, 195, 195, 0.62);
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0 5px;
  border-radius: 10px;
  justify-content: center;
}

._3fqTI:hover {
  color: white;
  background: linear-gradient(31deg, var(--lz-color-primary-light, #328B31) -28.78%, var(--lz-color-primary-light-1, #96CF24) 141.82%);
}

._3fqTI:hover span {
  color: white;
}

._1Vcm1 {
  flex-direction: column;
  grid-column: 1 / span 2;
}

._1Vcm1 span {
  font-size: 22px;
}

._1zEqA {
  display: flex;
  align-items: center;
  gap: 15px;
}

._3cHWs {
  display: flex;
  align-items: center;
  gap: 25px;
  width: 25px;
}

._2fOJ7 {
  text-align: center;
  color: #535353;
  font-weight: bold;
  font-size: 14px;
}

.dark ._2fOJ7 {
  color: #bbb;
}

._USxqz {
  display: flex;
  width: 320px;
  flex-direction: column;
  padding-bottom: 10px;
}

.dark ._USxqz {
  color: #bbb;
}


._USxqz>span:first-child {
  font-weight: bold;
  font-size: 22px;
  text-align: center;
  margin-bottom: 25px;
  margin-top: 25px;
}

._USxqz>span:nth-child(2) {
  text-align: center;
  margin-bottom: 20px;
}

._USxqz>div:nth-child(3) {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
}

._USxqz>div:nth-child(4) {
  text-align: center;
}

._19WfG {
  height: 52px;
  display: flex;
  align-items: center;
  border: 1px solid rgba(195, 195, 195, 0.62);
  border-radius: 15px;
  padding: 10px 30px;
  gap: 20px;
  text-decoration: none;
  color: #535353;
  font-size: 24px;
  font-weight: bold;
}

.dark ._19WfG {
  color: #bbb;
}

._19WfG:hover {
  color: white;
  background: linear-gradient(31deg, var(--lz-color-primary-light, #328B31) -28.78%, var(--lz-color-primary-light-1, #96CF24) 141.82%);
}

._3Vsch {
  color: #535353;
  cursor: pointer;
  font-size: 18px;
}

.dark ._3Vsch {
  color: #bbb;
}

._3Vsch {
  display: inline-block;
  border-bottom: 1px solid #535353;
  transition: all .2s;
  padding: 2px 10px;
}

.dark ._3Vsch {
  border-bottom: 1px solid #bbb;
}

._3Vsch:hover {
  transform: translateY(-1px);
}

._3Vsch:active {
  transform: translateY(0px);
}
