.swap-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  opacity: 0;
  visibility: hidden;
  transition: all 0.25s ease-out;
}
.swap-modal.show {
  visibility: visible;
  outline: none;
  text-decoration: none;
  font-size: 70%;
  opacity: 1;
}
.swap-modal .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background: rgba(42, 42, 42, 0.9019607843);
}
.swap-modal .modal {
  background: #1B1D21;
  border-radius: 20px;
  width: 40rem;
  max-height: 80%;
  z-index: 1001;
  padding: 1rem 1.5rem 2rem 1.5rem;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  margin-top: 12rem;
  overflow-y: auto;
}
.swap-modal .modal .btn-close-wrap {
  margin-bottom: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.swap-modal .modal .btn-close-wrap .btn-close {
  cursor: pointer;
}
.swap-modal .modal .btn-close-wrap .title {
  font-size: 2rem;
  font-weight: bold;
}
.swap-modal .modal .modal-content {
  padding: 1rem 0;
}
